import { ProxyDataResponse } from '../background/network/proxy/types/ProxyData';
import { httpClient } from './httpClient';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';

export async function fetchProxyData({
  apiBaseUrl,
  deviceId,
  deviceIp,
  onFail,
}: {
  apiBaseUrl: string;
  deviceId: string;
  deviceIp?: string;
  onFail?: boolean;
}): Promise<ProxyDataResponse> {
  const { featuresList } = await featureStore.getSnapshotAsync();

  const apiServerData = await httpClient
    .post<ProxyDataResponse>(`https://${apiBaseUrl}/api/v2/get-proxy`, {
      json: {
        product: process.env.PRODUCT,
        device_id: deviceId,
        device_ip: deviceIp ?? 'unknown', // Use 'unknown' if deviceIp is null
        on_fail: onFail,
        with_auth: featuresList[Feature.ProxyAuth],
      },
    })
    .json();
  return apiServerData;
}
